ontrol + label {\n  position: relative;\n  display: inline-block;\n  margin-bottom: 0;\n  padding-left: 1.625rem;\n  color: var(--brand-light-gray);\n  vertical-align: middle;\n  cursor: pointer;\n  transition: all ceaser($easeInOutQuint) 0.35s;\n\n  &.fancy-field-empty {\n    padding: 0 0;\n    height: 1rem;\n    width: 1rem;\n  }\n\n  &::before,\n  &::after {\n    position: absolute;\n    top: 50%;\n    left: 0;\n    content: '';\n    transform: translateY(-50%);\n    transition: all ceaser($easeInOutQuint) 0.35s;\n  }\n\n  &::before {\n    background: $brand-white;\n    border: 1px solid lighten($brand-light-gray, 50%);\n    border-radius: 2px;\n    height: 1rem;\n    width: 1rem;\n  }\n\n  &::after {\n    top: 0.23rem;\n    left: 0.37rem;\n    height: 9px;\n    width: 5px;\n    border-color: transparent;\n    border-style: solid;\n    border-width: 0 2px 2px 0;\n    transform: rotate(37deg); //~45deg\n  }\n}\n\ninput[type='checkbox'].fancy-field-control.checked-type-minus + label {\n  &::after {\n    top: 50%;\n    left: 0.25rem;\n    background: transparent;\n    height: 2px;\n    width: 0.5rem;\n    transform: translateY(-50%) translateX(0) rotate(0deg);\n  }\n}\n\ninput[type='checkbox'].fancy-field-control-lg + label.fancy-field-empty {\n  height: 1.25rem;\n  width: 1.25rem;\n\n  &::before {\n    height: 1.25rem;\n    width: 1.25rem;\n  }\n\n  &::after {\n    // top: 0.07rem;\n    width: 7px;\n    height: 10px;\n  }\n}\n\ninput[type='checkbox'].fancy-field-control-sm + label {\n  &::after {\n    top: 0.36rem;\n    @include media('>sm') {\n      top: 0.25rem;\n    }\n  }\n}\n\n// == Checked\ninput[type='checkbox'].fancy-field-control:checked + label {\n  color: var(--control-label-text-color);\n\n  &::before {\n    border-color: var(--brand-secondary);\n  }\n\n  &::after {\n    border-color: var(--brand-secondary);\n  }\n}\n\ninput[type='checkbox'].fancy-field-control.checked-type-minus:checked + label {\n  &::after {\n    background: var(--brand-secondary);\n  }\n}\n\n// == Switch toggle fancy field\n.toggle-switcher {\n  display: flex;\n  align-items: center;\n  line-height: 1;\n}\n\n.toggle-switcher-control + label {\n  position: relative;\n  margin-bottom: 0;\n  margin-right: 1rem;\n  background: var(--brand-faded);\n  border: 1px solid $input-border-color;\n  border-radius: 0.625rem;\n  height: 1.25rem;\n  width: 3.75rem;\n  transition: all ceaser($easeInOutQuint) 0.35s;\n\n  &::before,\n  &::after {\n    position: absolute;\n    top: 50%;\n    left: 2px;\n    border-radius: 0.5rem;\n    color: rgba(0, 0, 0, 0.12);\n    font-size: 0.5rem;\n    width: 1.75rem;\n    height: 1rem;\n    line-height: 1rem;\n    text-align: center;\n    transform: translateY(-50%);\n    transition: all ceaser($easeInOutQuint) 0.35s;\n  }\n\n  &::before {\n    content: 'ON';\n    width: 1.5rem;\n    background: transparent;\n  }\n\n  &::after {\n    right: 2px;\n    left: auto;\n    content: 'OFF';\n    background: #e0e0e0;\n  }\n\n  + .toggle-switcher-text {\n    color: var(--brand-light-gray);\n  }\n}\n\n.toggle-switcher-control:checked + label {\n  &::before {\n    background: var(--brand-secondary);\n    color: var(--text-color);\n    width: 1.75rem;\n  }\n\n  &::after {\n    background: transparent;\n    width: 1.5rem;\n  }\n\n  + .toggle-switcher-text {\n    color: var(--text-color);\n  }\n}\n","/**\n* Hero banner scss file which includes hero baner style rules\n*/\n\n// == Home page hero style\n.hero {\n  position: relative;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  // background: linear-gradient(#34495e,rgba(44,62,80,.1));\n}\n\n.hero-home {\n  min-height: 35rem;\n\n  // Add shadow to non-squared/non-rectangular image\n  filter: drop-shadow(0px 4px 8px rgba(0, 0, 0, 0.38));\n\n  @include media('>sm') {\n    min-height: 35rem;\n  }\n  @include media('<ipad', '>=870px') {\n    min-height: 35rem;\n  }\n  @include media('>=laptop') {\n    min-height: 25rem;\n  }\n\n  @include media('>=ipad') {\n    paddin