\n      color: var(--brand-gray) !important;\n    }\n  }\n}\n\n.dropdown-toggle {\n  &::after,\n  &.hide-arrow::before {\n    display: none !important;\n  }\n\n  &::before {\n    position: absolute;\n    top: 50%;\n    right: 0.6rem;\n    margin-top: -0.1rem; //~-0.1rem;\n    content: '';\n    border-color: $gray-9e9e9e;\n    border-style: solid;\n    border-width: 0.0938rem 0.0938rem 0 0;\n    width: 0.41rem;\n    height: 0.41rem;\n    transform: translateY(-50%) rotate(135deg);\n    transition: all ceaser($ease) 0.2s;\n\n    @include media('>=ipad') {\n      margin-top: -0.075rem;\n    }\n  }\n}\n\n.show > .dropdown-toggle {\n  &::before {\n    margin-top: 0.1rem;\n    transform: translateY(-50%) rotate(-45deg);\n  }\n}\n\n.btn-dropdown {\n  // width: 2.875rem;\n  // height: 1.875rem;\n\n  .dropdown-toggle {\n    position: relative;\n    padding: 0.5rem 0.5rem 0.5rem;\n    background: var(--dropdown-toggle-bg);\n    border: solid 1px var(--dropdown-border-color);\n    border-radius: 4px;\n    color: var(--dropdown-toogle-color);\n    cursor: pointer;\n    font-family: $font-family-lato;\n    font-size: 1rem;\n    line-height: 1.14;\n    height: 2.5rem;\n    width: 4.375rem;\n    text-align: left;\n  }\n\n  // == large\n  &-sm {\n    .dropdown-toggle {\n      font-size: 0.75rem;\n\n      @include media('>sm') {\n      }\n    }\n  }\n\n  // == large\n  &-lg {\n    .dropdown-toggle {\n      width: 100%;\n      @include media('>sm') {\n        // height: 2.5rem;\n      }\n    }\n  }\n}\n\n.lang-selector-dropdown {\n  .dropdown-toggle {\n    padding: 0.325rem 0.4rem 0.48899rem;\n    border-color: rgba(224, 224, 224, 0.22);\n    border-radius: 2px;\n    color: $brand-white;\n    font-size: 0.875rem;\n    height: 1.875rem;\n    width: 2.875rem;\n\n    @include media('>sm') {\n      height: 1.875rem;\n      width: 4.625rem;\n      .fa {\n        margin-right: 0.33rem;\n      }\n\n      &::before {\n        right: 0.7rem;\n      }\n    }\n\n    @include media('>=ipad') {\n      height: 2rem;\n      padding: 0.43rem 0.65rem 0.48899rem;\n    }\n\n    &::before {\n      border-color: $brand-white;\n    }\n  }\n\n  .dropdown-menu {\n    border-radius: 2px;\n    min-width: 4.625rem;\n  }\n}\n\n.dropdown-item {\n  padding-top: 1.25rem;\n  padding-bottom: 1.25rem;\n  cursor: pointer;\n\n  &:not(:last-child) {\n    border-bottom: 1px solid rgba(224, 224, 224, 0.5);\n  }\n}\n\n// == Dropdown type select\n.btn-dropdown-select {\n  .dropdown-menu {\n    margin-top: -1px;\n    border-top: 0;\n    border-top-left-radius: 0;\n    border-top-right-radius: 0;\n    text-align: left;\n    transform: none;\n    transition: none;\n    visibility: visible;\n  }\n\n  .show {\n    .dropdown-toggle {\n      border-bottom-right-radius: 0;\n      border-bottom-left-radius: 0;\n    }\n  }\n\n  .dropdown-item {\n    padding: 0.625rem 0.75rem;\n    border-bottom: 0;\n    line-height: 1.15;\n  }\n}\n","/**\n* Overriding bootstrap modal\n*/\n\n.close {\n  transition: all ceaser($easeInOutQuint) 0.35s;\n}\n\n.modal-sm .close {\n  margin-left: 0.25rem;\n  font-size: 1.75rem;\n}\n\n.modal,\n.modal-lg {\n  border-radius: 2px;\n  // text-align: center;\n\n  .modal-dialog {\n    margin-right: auto;\n    margin-left: auto;\n    width: calc(100% - 30px);\n  }\n}\n\n.modal-header {\n  padding: 0.75rem 0.938rem;\n  background: var(--modal-header-background);\n  border-radius: 2px;\n  line-height: 1;\n\n  @include media('>sm') {\n    padding: 0.75rem 1.25rem 0.75rem 1.5rem;\n  }\n\n  .modal-lg &,\n  .modal-md & {\n    @include media('>sm') {\n      padding: 1.532rem 1.75rem;\n      max-height: 5rem;\n    }\n\n    @include media('<=sm') {\n      .modal-title {\n        font-size: 1rem;\n      }\n    }\n  }\n}\n\n.modal-title {\n  font-size: 1rem;\n  line-height: 1;\n\n  .modal-sm & {\n    @include media('>=ipad') {\n      font-size: 0.875rem;\n    }\n  }\n}\n\n.modal-body {\n  padding: 1.125rem 0.938rem;\n  border-bottom-left-radius: $border-radius-lg;\n  border-bottom-right-radius: $border-radius-lg;\n\n  @include media('>sm') {\n    padding: 1.563rem 1.5rem 1.563rem 1.5rem;\n  }\n}\n\n.modal-footer {\n  padding-right: 1.5rem;\n  padding-left: 1.5rem;\n  background: var(--modal-header-background);\n  border-radius: 2px;\n}\n\n.modal-type-default,\n.modal-md {\n  .modal-body {\n    padding: 1.125rem 0.938rem 1.5rem 0.938rem;\n\n    @include media('>sm') {\n      padding: 1.875rem 1.563rem 3.125rem 1.563rem;\n    }\n  }\n}\n\n// .modal::before {\n//     content: '';\n//     display: inline-block;\n//     height: 100%;\n//     vertical-align: middle;\n//     margin-right: -5px;\n// }\n\n// .modal-dialog {\n//     display: inline-block;\n//     text-align: left;\n//     vertical-align: middle;\n// }\n\n// == Mailbox modal\n.mailbox-modal {\n  .modal-dialog {\n    max-width: 480px;\n    // width: 100%;\n  }\n}\n\n.modal-lg .modal-dialog {\n  max-width: 650px;\n\n  @include media('>sm') {\n    .modal-body {\n      padding: 1.25rem 2.5rem 1.875rem;\n    }\n  }\n\n  @include media('>=ipad') {\n    .modal-body {\n      padding: 1.25rem 4.375rem 1.875rem;\n    }\n  }\n}\n\n.modal-sm .modal-dialog {\n  max-width: 550px;\n\n  @include media('<=sm') {\n    .modal-body {\n      padding: 1.25rem 0.938rem;\n    }\n  }\n}\n\n.modal-md .modal-dialog {\n  max-width: 555px;\n\n  @include media('<=sm') {\n    .modal-body {\n      padding: 1.25rem 0.938rem;\n    }\n  }\n}\n\n.modal-sm .modal-dialog {\n  .btn {\n    font-size: 0.75rem;\n    @include media('<=sm') {\n      min-width: inherit;\n      padding: 0.5633rem 0.5rem;\n    }\n  }\n}\n\n// == Billing information form\n.modal .form-card-holder {\n  max-width: inherit;\n\n  .form-step-card-title {\n    font-size: 1rem;\n  }\n}\n\n// == Vertically centered\n.modal-dialog-centered {\n  display: flex;\n  align-items: center;\n  pointer-events: none;\n  min-height: calc(100% - 1.85rem);\n\n  @include media('>sm') {\n    min-height: calc(100% - 3.75rem);\n  }\n\n  .modal-content {\n    pointer-events: auto;\n    width: 100%;\n  }\n}\n","/**\n* Bootstrap overrides :: alert scss file\n*/\n\n// == Alert\n.l-alert {\n  padding: 0.688rem 0.938rem;\n  border: 1px solid #bdbdbd !important;\n  border-radius: 2px;\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.12);\n  color: $brand-black;\n  font-size: 0.75rem;\n  width: 100%;\n\n  @include media('>sm') {\n    padding: 0.688rem 1.563rem;\n  }\n\n  a {\n    color: $brand-black;\n  }\n}\n\n.l-alert-warning {\n  background-color: #fff2df !important;\n}\n\n.l-alert-danger {\n  background-color: #ffe1df !important;\n}\n\n.l-alert-success {\n  background-color: #dff2ff !important;\n}\n\n.l-alert-is-floated {\n  position: absolute;\n  top: 0px;\n  right: 0;\n  left: 0;\n  z-index: $zindex-default;\n\n  @include media('>sm') {\n    top: 77px;\n    right: auto;\n    left: 50%;\n    transform: translateX(-50%);\n  }\n}\n","/**\n* Bootstrap overrides :: alert scss file\n*/\n\n// == Tooltips\n.tooltip {\n  font-size: 0.625rem;\n  white-space: nowrap;\n\n  &-inner {\n    border-radius: 2px;\n    max-width: none;\n  }\n\n  @include media('<=sm') {\n    display: none !important;\n  }\n}\n\n.tooltip > .arrow {\n  position: absolute;\n  display: block;\n  width: 0.8rem;\n  height: 0.4rem;\n\n  &::before {\n    position: absolute;\n    content: '';\n    border-color: transparent;\n    border-style: solid;\n  }\n}\n\n.bs-tooltip-top {\n  padding: 0.25rem 0;\n  left: calc(50% - 0.4rem);\n\n  .arrow {\n    bottom: 0;\n\n    &::before {\n      top: 0;\n      border-width: 0.4rem 0.4rem 0;\n      border-top-color: #000;\n    }\n  }\n}\n\n.bs-tooltip-auto[x-placement^='bottom'],\n.bs-tooltip-bottom {\n  padding: 0.25rem 0;\n}\n\n.bs-tooltip-auto[x-placement^='bottom'] .arrow,\n.bs-tooltip-bottom .arrow {\n  top: 0;\n}\n\n.bs-tooltip-auto[x-placement^='bottom'] .arrow:before,\n.bs-tooltip-bottom .arrow:before {\n  bottom: 0;\n  border-width: 0 0.4rem 0.4rem;\n  border-bottom-color: #000000;\n}\n","/**\n* @partial: _inbox.scss\n*/\n\n// == Create account pag